@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
}

html {
  border: 15px;  
}

body {
  font-family: "Montserrat", sans-serif;
}

.App {
  min-height: 100vh;
  background-image: -webkit-linear-gradient(330deg, #d4fc79 0%, #96e6a1 100%);
  background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%)
}

.header {
  text-align: center;
  padding: 20px 40px 60px 40px;
}

.title {
  color: #565656;
  font-weight: 200;
  font-size: 3rem;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.tb
{
    display: table;
    width: 100%;
}

.td
{
    display: table-cell;
    vertical-align: middle;
}

input, button
{
    color: #fff;
    font-family: "Montserrat", sans-serif;
    padding: 0;
    margin: 0;
    border: 0;
    background-color: transparent;
}

#cover
{
    position: absolute;
    top: 30%;
    left: 0;
    right: 0;
    width: 550px;
    padding: 1px;
    margin: -80px auto 0 auto;
    background-image: -webkit-gradient(linear, left bottom, left top, from(#9be15d), to(#00e3ae));
    background-image: -webkit-linear-gradient(bottom, #9be15d 0%, #00e3ae 100%);
    background-image: linear-gradient(to top, #9be15d 0%, #00e3ae 100%);
    border-radius: 20px;
    box-shadow: 0 10px 40px #ff7c7c, 0 0 0 20px #ffffffeb;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
}

form
{
    height: 96px;
}

input[type="text"]
{
    width: 100%;
    height: 96px;
    font-size: 60px;
    line-height: 1;
}

input[type="text"]::-webkit-input-placeholder
{
    color: #565656;
}

input[type="text"]::-moz-placeholder
{
    color: #565656;
}

input[type="text"]:-ms-input-placeholder
{
    color: #565656;
}

input[type="text"]::-ms-input-placeholder
{
    color: #565656;
}

input[type="text"]::placeholder
{
    color: #565656;
}

#s-cover
{
    width: 1px;
    padding-left: 35px;
}

button
{
    position: relative;
    display: block;
    width: 84px;
    height: 96px;
    cursor: pointer;
}

#s-circle
{
    position: relative;
    top: -1px;
    left: 0;
    width: 70px;
    height: 70px;
    margin-top: 0;
    border-width: 15px;
    border: 15px solid #fff;
    background-color: transparent;
    border-radius: 50%;
    -webkit-transition: 0.5s ease all;
    transition: 0.5s ease all;
}

button span
{
    position: absolute;
    top: 68px;
    left: 43px;
    display: block;
    width: 45px;
    height: 15px;
    background-color: transparent;
    border-radius: 10px;
    -webkit-transform: rotateZ(52deg);
            transform: rotateZ(52deg);
    -webkit-transition: 0.5s ease all;
    transition: 0.5s ease all;
}

button span:before, button span:after
{
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 45px;
    height: 15px;
    background-color: #fff;
    border-radius: 10px;
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
    -webkit-transition: 0.5s ease all;
    transition: 0.5s ease all;
}

#s-cover:hover #s-circle
{
    top: -1px;
    width: 67px;
    height: 15px;
    border-width: 0;
    background-color: #fff;
    border-radius: 20px;
}

#s-cover:hover span
{
    top: 50%;
    left: 56px;
    width: 25px;
    margin-top: -9px;
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
}

#s-cover:hover button span:before
{
    bottom: 11px;
    -webkit-transform: rotateZ(52deg);
            transform: rotateZ(52deg);
}

#s-cover:hover button span:after
{
    bottom: -11px;
    -webkit-transform: rotateZ(-52deg);
            transform: rotateZ(-52deg);
}
#s-cover:hover button span:before, #s-cover:hover button span:after
{
    right: -6px;
    width: 40px;
    background-color: #fff;
}


.recipe {
  border-radius: 20px;
  padding: 20px 0;
  margin: 20px;
  width: 400px;
  box-shadow: 8px 5px 5px 2px #9e9c9c26;
  letter-spacing: 1px;
  border: 1px solid #80808024;
  background-image: -webkit-linear-gradient(330deg, #fdfbfb 0%, #ebedee 100%);
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
}

.recipe:hover,
.recipe:active {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  -webkit-transition: -webkit-transform 200ms ease-in-out;
  transition: -webkit-transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
}

.recipe_title {
  text-align: center;
  font-size: 1.6rem;
  color: #565656;
}

.recipe h2 {
  margin: 0;
  padding: 10px 20px 0px 20px;
  color: #565656;
  font-size: 1.3rem;
}

.image_div {
  width: 100%;
  overflow: hidden;
  margin: 0 auto;
}

.image {
  display: block;
  width: 100%;
  margin: -20% 0;
}

.calories {
  text-align: center;
}

.ingredients {
  list-style-type: none;
  padding: 10px 20px;
}

.ingredient {
  padding: 5px 0;
  line-height: 1.4rem;
}

.ingredient i {
  color: #96e6a1;
}

